@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
:root {
   --main-color: #028B82;
   --color-dark: #1D2231;
   --text-grey: #8390A2;
}

* {
   padding: 0;
   margin: 0;
   box-sizing: border-box;
   list-style-type: none;
   text-decoration: none;
   font-family: 'Poppins', sans-serif;
}

.sidebar-menu .profile-details {
   position: fixed;
   bottom: 0;
   display: flex;
   align-items: center;
   justify-content: space-between;
   background: #1d1b31;
   padding: 6px 0;
   transition: all 0.5s ease;
}

.sidebar {
   width: 250px;
   position: fixed;
   left: 0;
   top: 0;
   height: 100%;
   background: var(--main-color);
   z-index: 100;
   transition: width 300ms;
}

.sidebar-brand {
   height: 90px;
   padding: 1rem 0rem 1rem 2rem;
   color: #fff;
}

.sidebar-brand span {
   display: inline-block;
   padding-right: 1rem;
}

.sidebar-menu {
   margin-top: 1rem;
}

.sidebar-menu li {
   width: 100%;
   margin-bottom: 1.7rem;
   padding-left: 1rem;
}

.sidebar-menu a {
   padding-left: 1rem;
   display: block;
   color: #fff;
   font-size: 1.1rem;
}

.sidebar-menu a.active {
   background: #fff !important;
   padding-top: 1rem;
   padding-bottom: 1rem;
   color: var(--main-color) !important;
   border-radius: 30px 0px 0px 30px;
}

.sidebar-menu a span:first-child {
   font-size: 1.5rem;
   padding-right: 1rem;
}

#nav-toggle:checked+.sidebar {
   width: 70px;
}

#nav-toggle:checked+.sidebar .sidebar-brand,
#nav-toggle:checked+.sidebar li {
   padding-left: 1rem;
   text-align: center;
}
/* #nav-toggle:checked+.sidebar li a {
   padding-left: 0rem;
} */

#nav-toggle:checked+.sidebar .sidebar-brand h2 span:last-child,
#nav-toggle:checked+.sidebar li a span:last-child {
   display: none;
}

#nav-toggle:checked~.main-content {
   margin-left: 70px;
}

#nav-toggle:checked~.main-content header {
   width: calc(100% - 70px);
   left: 70px;
}
/* #nav-toggle:checked+.sidebar .sidebar-brand.lab {
   display: inline !important;
} */

ul {
   padding-left: 0 !important;
}

.main-content {
   transition: margin-left 300ms;
   margin-left: 250px;
}

a {
   text-decoration: none !important;
}

h2 {
   font-size: 24px !important;
}

h4 {
   font-size: 16px !important;
}

header {
   background: #fff;
   display: flex;
   justify-content: space-between;
   padding: 1rem 1.5rem;
   box-shadow: 2px 2px 5px rgb(0 0 0 / 20%);
   position: fixed;
   left: 250px;
   width: calc(100% - 250px);
   top: 0;
   z-index: 100;
   transition: width 300ms;
}

#nav-toggle {
   display: none;
}

header h2 {
   color: #222;
}

header label span {
   font-size: 1.7rem;
   padding-right: 1rem;
}

.search-wrapper {
   border: 1px solid #ccc;
   border-radius: 30px;
   height: 50px;
   display: flex;
   align-items: center;
   overflow-x: hidden;
}

.search-wrapper span {
   display: inline-block;
   padding: 0rem 1rem;
   font-size: 1.5rem;
}

.search-wrapper input {
   height: 100%;
   padding: .5rem;
   border: none;
   outline: none;
}

.user-wrapper {
   display: flex;
   align-items: center;
}

.user-wrapper img {
   border-radius: 50%;
   margin-right: 1rem;
}

.user-wrapper small {
   display: inline-block;
   color: var(--text-grey);
}

main {
   margin-top: 85px;
   padding: 2rem 1.5rem;
   background: #f1f5f9;
   min-height: calc(100vh - 90px);
}

a.signout {
   bottom: 0;
   position: fixed;
   padding: 10px;
}

.cards {
   display: grid;
   grid-template-columns: repeat(1, 1fr);
   grid-gap: 2rem;
   margin-top: 1rem;
}

.card-single {
   display: flex;
   justify-content: space-between;
   background: #fff;
   padding: 2rem;
   border-radius: 20px;
   margin-top: 2rem;
}
/* Dashboard */

.content-table {
   border-collapse: collapse;
   margin: 25px 0;
   font-size: 0.9em;
   min-width: 400px;
   border-radius: 5px 5px 0 0;
   overflow: hidden;
   box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.content-table thead tr {
   background-color: #009879;
   color: #ffffff;
   text-align: center;
   font-weight: bold;
}

.content-table th,
.content-table td {
   padding: 12px 10px;
   text-align: center;
}

.content-table tbody tr {
   border-bottom: 1px solid #dddddd;
   text-align: center;
}

.content-table tbody tr:nth-of-type(even) {
   background-color: #f3f3f3;
}

.content-table tbody tr:last-of-type {
   border-bottom: 2px solid #009879;
}

.content-table tbody tr.active-row {
   font-weight: bold;
   color: #009879;
}

td {
   text-align: center;
}

li:hover {
   color: white !important;
}

@media only screen and (max-width: 1200px) {
   .sidebar {
       width: 70px;
   }
   .sidebar .sidebar-brand,
   .sidebar li {
       padding-left: 1rem;
       text-align: center;
   }
   .sidebar li a {
       padding-left: 1rem !important;
   }
   .sidebar .sidebar-brand h2 span:last-child,
   .sidebar li a span:last-child {
       display: none;
   }
   .main-content {
       margin-left: 70px;
   }
   .main-content header {
       width: calc(100% - 70px);
       left: 70px;
   }
}

@media only screen and (max-width: 960px) {
   .cards {
       grid-template-columns: repeat(1, 1fr);
   }
   .recent-grid {
       grid-template-columns: 50% 50%;
   }
}

@media only screen and (max-width: 768px) {
   .cards {
       grid-template-columns: repeat(1, 1fr);
   }
   .recent-grid {
       grid-template-columns: 100%;
   }
   .search-wrapper {
       display: none;
   }
   .sidebar {
       left: -100% !important;
   }
   header h2 {
       display: flex;
       align-items: center;
   }
   header h2 label {
       background: var(--main-color);
       padding-left: 0rem;
       margin-right: 1rem;
       height: 40px;
       width: 40px;
       display: inline-block;
       border-radius: 50%;
       color: #fff;
       display: flex;
       align-items: center;
       justify-content: center !important;
   }
   header h2 span {
       text-align: center;
       padding-right: 0rem;
   }
   header h2 {
       font-size: 1.1rem;
   }
   .main-content {
       width: 100%;
       margin-left: 0rem;
   }
   header {
       width: 100% !important;
       left: 0 !important;
   }
   #nav-toggle:checked+.sidebar {
       left: 0 !important;
       z-index: 100;
       width: 250px;
   }
   #nav-toggle:checked+.sidebar .sidebar-brand,
   #nav-toggle:checked+.sidebar li {
       padding-left: 2rem;
       text-align: left;
   }
   #nav-toggle:checked+.sidebar li a {
       padding-left: 1rem;
   }
   #nav-toggle:checked+.sidebar .sidebar-brand h2:last-child,
   #nav-toggle:checked+.sidebar li a span:last-child {
       display: inline;
   }
   #nav-toggle:checked~.main-content {
       margin-left: 0rem !important;
   }
}

@media only screen and (max-width: 560px) {
   .cards {
       grid-template-columns: 100%;
   }
}
th {
    height: 62px;
}
